body {
  background-color: #212121;
  margin: 0;
}

header {
  height: 60px;
  width: 100%;
  display: flex;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.form-search {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery-sect {
  width: 100%;
}

.input-search {
  width: 60%;
  height: 80%;
  border: 0;
  padding-left: 10px;
  font-size: 20px;
}

.button-search {
  width: 100px;
  height: 40px;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  color: #fff;
  background: none;
  border: 1px solid #3d6aff;
  border-radius: 7px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 12px;
  font-weight: 600;
  transition: all .2s ease-in;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 rgba(0, 0, 0, 0);
}

button:hover {
  background: #3d6aff;
  transition: all .2s ease-out;
  box-shadow: 0 0 30px 5px rgba(0, 142, 236, .816);
}

button:hover:before {
  animation: sh02 .5s linear;
}

button:before {
  content: "";
  width: 0;
  height: 86%;
  opacity: 0;
  background: #fff;
  display: block;
  position: absolute;
  top: 7%;
  left: 0%;
  transform: skewX(-20deg);
  box-shadow: 0 0 50px 30px #fff;
}

@keyframes sh02 {
  from {
    opacity: 0;
    left: 0%;
  }

  50% {
    opacity: 1;
  }

  to {
    opacity: 0;
    left: 100%;
  }
}

button:active {
  transition: box-shadow .2s ease-in;
  box-shadow: 0 0 rgba(0, 0, 0, 0);
}

.input-search:focus {
  border: 0;
}

.info {
  background-color: #fff;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.main-gallery {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 20%;
  box-sizing: border-box;
  flex-direction: column;
  padding: 20px;
  display: flex;
}

